@charset "UTF-8";

.title {
  position: absolute;
  top: 50%;
  left: 50%;
  color: rgba(255, 255, 255, 0.747);
  text-shadow: 0 0 20px violet;
  font-size: 2rem;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  z-index: 1;
}

section {
  width: 100%;
  height: 100vh;
  background: rgb(22, 22, 22);
  overflow: hidden;
  user-select: none;
}

section .row {
  display: flex;
  white-space: nowrap;
  font-size: 2rem;
  padding: 10px 0;
  transform: rotate(-30deg);
  position: relative;
  top: -40%;
}

section .row div {
  animation: animate 60s linear infinite;
}

i {
  color: rgba(0, 0, 0, 0.5);
  transition: 1s;
}

i:hover {
  color: violet;
  text-shadow: 0 0 12px violet;
  transition: 0s;
}

@keyframes animate {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-100%);
  }
}
